<template>
  <div class="modal fade" id="ArticlesModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true" ref="modal">
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">文章資訊</h5>
          <button type="button" class="btn-close"
                  data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-3">
                <label for="price" class="form-label">上傳圖片</label>
                <input
                type="file"
                id="customFile"
                class="form-control"
                ref="fileInput"
                @change="uploadFile"
                />
                <img class="img-fluid mb-3 pt-3" :src="tempArticle.image" alt="" />
              </div>
            </div>
            <div class="col-sm-8">
              <div class="mb-3">
                <label for="title" class="form-label">標題</label>
                <input type="text" class="form-control" id="title" v-model="tempArticle.title"
                      placeholder="請輸入標題">
              </div>
              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="origin_price" class="form-label">作者</label>
                  <input type="text" class="form-control" id="author" v-model="tempArticle.author"
                      placeholder="請輸入作者">
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label">建立日期</label>
                  <input type="date" class="form-control" id="create_at"
                      v-model="create_at">
                </div>
              </div>
              <hr>
              <div class="mb-3">
                <label for="tag" class="form-label">標籤</label>
                <input type="text" class="form-control" id="tag" v-model="tempArticle.tag"
                      placeholder="請輸入文章標籤">
              </div>
              <div class="mb-3">
                <label for="description" class="form-label">簡介</label>
                <textarea type="text" class="form-control" id="description"
                  placeholder="請輸入文章描述" v-model="tempArticle.description"></textarea>
              </div>
              <div class="mb-3">
                <label for="content" class="form-label">內容</label>
                <textarea name="" class="form-control" id="tag" cols="30" rows="10" v-model="tempArticle.content" placeholder="請輸入內容"></textarea>
              </div>
              <div class="mb-3">
                <div class="form-check text-start">
                  <input class="form-check-input" type="checkbox"
                        v-model="tempArticle.isPublic" id="isPublic">
                  <label class="form-check-label" for="isPublic">
                    是否啟用
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
          <button type="button" class="btn btn-primary"
            @click="$emit('update-article',tempArticle)">更新
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import modalMixin from '../mixins/modalMixin'

export default {
  name: 'ArticlesModal',
  props: {
    article: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  mixins: [
    modalMixin
  ],
  data () {
    return {
      modal: {},
      tempArticle: {},
      create_at: ''
    }
  },
  watch: {
    article () {
      this.tempArticle = this.article
      // console.log(this.tempArticle.create_at)
      const dateAndTime = new Date(this.tempArticle.create_at * 1000)
        .toISOString().split('T');
      [this.create_at] = dateAndTime
    },
    create_at () {
      this.tempArticle.create_at = Math.floor(new Date(this.create_at) / 1000)
    }
  },
  methods: {
    uploadFile () {
      const uploadFile = this.$refs.fileInput.files[0]
      const formData = new FormData()
      formData.append('file-to-upload', uploadFile)
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`
      this.$http.post(api, formData).then((res) => {
        // console.log(res.data)
        if (res.data.success) {
          this.tempArticle.image = res.data.imageUrl
        }
      })
    }
  }
}
</script>
