<template>
  <loading :active="isLoading"></loading>
  <div class="text-end mt-4">
    <button class="btn btn-primary" @click.prevent="openModal(true)">
      新增文章
    </button>
  </div>
  <div>
    <table class="table mt-4">
      <thead>
      <tr>
        <th>標題</th>
        <th>作者</th>
        <th>建立時間</th>
        <th>是否開啟</th>
        <th>編輯</th>
      </tr>
      </thead>
      <tbody>
        <tr v-for="item in articles" :key="item">
          <td>{{ item.title }}</td>
          <td>{{ item.author }}</td>
          <td>{{ $filters.date(item.create_at) }}</td>
          <td>
            <span class="text-success" v-if="item.isPublic">啟用</span>
            <span class="text-muted" v-else>未啟用</span>
          </td>
          <td>
            <div class="btn-group">
              <button class="btn btn-outline-primary btn-sm" @click.prevent="openModal(false, item)">檢視</button>
              <button class="btn btn-outline-danger btn-sm" @click="openDeleteModal(item)">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination :pages="pagination" @chang-page="changPage"></Pagination>
    <ArticlesModal ref="ArticlesModal" :article="tempArticle" @update-article="updateArticle"></ArticlesModal>
    <DeleteModal ref="ArticlesDelModal" :item="tempArticle" @del-item="deleteData"></DeleteModal>
  </div>
</template>
<script>
import Pagination from '../components/Pagination.vue'
import ArticlesModal from '../components/ArticlesModal.vue'
import DeleteModal from '../components/DelModal.vue'
export default {
  components: {
    Pagination,
    ArticlesModal,
    DeleteModal
  },
  data () {
    return {
      articles: [],
      pagination: {},
      tempArticle: {
        title: '',
        description: '',
        image: '',
        tag: [],
        author: '',
        isPublic: false,
        content: ''
      },
      isLoading: false,
      isNew: false
    }
  },
  methods: {
    getData (page = 1) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/articles?page=${page}`
      this.$http.get(api).then((res) => {
        this.isLoading = false
        if (res.data.success) {
          this.articles = res.data.articles
          this.pagination = res.data.pagination
        }
      })
    },
    changPage (page) {
      this.getData(page)
    },
    openModal (isNew, item) {
      this.isNew = isNew
      if (this.isNew) {
        this.tempArticle = {
          create_at: new Date().getTime() / 1000
        }
      } else {
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/article/${item.id}`
        this.$http.get(url).then((res) => {
          this.tempArticle = { ...res.data.article }
        })
      }
      this.$refs.ArticlesModal.showModal()
    },
    openDeleteModal (item) {
      this.tempArticle = { ...item }
      this.$refs.ArticlesDelModal.showModal()
    },
    updateArticle (item) {
      this.tempArticle = item
      let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/article`
      let httpMethods = 'post'
      let methodsTxt = '新增'
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/article/${item.id}`
        httpMethods = 'put'
        methodsTxt = '修改'
      }
      this.$http[httpMethods](api, { data: this.tempArticle }).then((res) => {
        this.$refs.ArticlesModal.hideModal()
        if (res.data.success) {
          this.getData()
          this.$httpMessage(res, methodsTxt)
        } else {
          this.$httpMessage(res, methodsTxt)
        }
      })
    },
    deleteData () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/article/${this.tempArticle.id}`
      this.$http.delete(api).then((res) => {
        if (res.data.success) {
          this.getData()
          this.$httpMessage(res, '刪除')
        } else {
          this.$httpMessage(res, '刪除')
        }
        this.$refs.ArticlesDelModal.hideModal()
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>
